import { Injectable } from '@angular/core';

@Injectable()
export class ValidateService {

  constructor() { }

  // Register validation
  validateRegister(user){
    if(
      user.name == undefined ||
      user.email == undefined ||
      user.username == undefined ||
      user.password == undefined ||
      user.passwordRepeat == undefined){
      return false;
    } else {
      return true;
    }
  }

  // Validate email
  validateEmail(email){
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/; // Email regex
    return re.test(email);
  }

  // Validate password match
  validatePasswordMatch(password, passwordRe) {
    if(password != passwordRe){
      return false;
    } else {
      return true
    }
  }

  // Validate login
  validateLogin(user){
    if(user.username == undefined || user.password == undefined){
      return false;
    } else {
      return true;
    } 
  }

  // Validate update password
  validateUpdatePassword(user){
    if(user.currentPassword == undefined || user.newPassword == undefined || user.newPasswordRepeat == undefined){
      return false;
    } else {
      return true;
    }
  }

}




