import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http'; // Http & headers for sending http request to back end
import 'rxjs/add/operator/map'; // For mapping response from the server
import { tokenNotExpired } from 'angular2-jwt';

@Injectable()
export class AuthService {

  authToken: any;
  user: any;

  constructor(private http: Http) { }

  // Register user
  registerUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json'); // Add content type to header
    return this.http.post('/users/register', user, {headers: headers}) // send a http post request to server with the details and map the result to json
      .map(res => res.json());
  }

  // Login user
  authenticateUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('/users/authenticate', user, {headers: headers})
      .map(res => res.json());
  }

  // Get profile
  getProfile(){
    let headers = new Headers();
    this.loadToken(); // loads the current token from the browser
    headers.append('Authorization', this.authToken); // Adds the token authorization to the request
    headers.append('Content-Type', 'application/json');
    return this.http.get('/users/profile', {headers: headers})
      .map(res => res.json());

  }


  // Stores user token and info to the browser local storage
  storeUserData(token, user){
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));

    this.authToken = token;
    this.user = user;
  }

  // Load token
  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  // Logged in
  loggedIn(){
    return tokenNotExpired();
  }

  // Logout
  logout(){
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }

  // Update password
  updatePassword(user){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.put('/users/update-password', user, {headers: headers})
      .map(res => res.json());
  }

}



