import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  username: String;
  password: String;

  constructor(
    private router: Router,
    private flashMessage: FlashMessagesService,
    private validateService: ValidateService,
    private authService: AuthService
  ) { }

  ngOnInit() {
  }

  // Login method
  onLoginSubmit(){

    const user = {
      username: this.username,
      password: this.password
    }

    // Validate fields
    if(!this.validateService.validateLogin(user)){
      this.flashMessage.show('Please fill in all required fields', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    this.authService.authenticateUser(user).subscribe(data => {
      if(data.success){ // If login successfull
        this.authService.storeUserData(data.token, data.user); // Store token and user info to the browser local storage
        this.flashMessage.show(data.msg, {cssClass: 'alert-success', timeout: 4000}); // Flash message
        this.router.navigate(['/dashboard']); // Redirect to the dashboard
      } else {
        this.flashMessage.show(data.msg, {cssClass: 'alert-danger', timeout: 4000});
        this.router.navigate(['/login']);
      }
    });

  }

}



