import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  name: String;
  email: String;
  username: String;
  password: String;
  passwordRepeat: String;

  passwordsMatch: boolean;

  constructor(
    private router: Router,
    private flashMessage: FlashMessagesService,
    private validateService: ValidateService,
    private authService: AuthService
  ) { }

  ngOnInit() {
  }

  onRegisterSubmit(){
    const user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password,
      passwordRepeat: this.passwordRepeat
    }

    // Validate fields
    if(!this.validateService.validateRegister(user)){
      this.flashMessage.show('Please fill in required fields', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    // Validate email
    if(!this.validateService.validateEmail(user.email)){
      this.flashMessage.show('Please enter a valid email', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    // Validate passwords (match)
    if(!this.validateService.validatePasswordMatch(user.password, user.passwordRepeat)){
      this.flashMessage.show('Passwords must match', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    // Register user
    this.authService.registerUser(user).subscribe(data => {
      if(data.success){ // If successfull
        this.flashMessage.show(data.msg, {cssClass: 'alert-success', timeout: 3000}); // Show message
        this.router.navigate(['/login']); // Redirect to the login page
      } else {
        this.flashMessage.show(data.msg, {cssClass: 'alert-danger', timeout: 3000}); // Show message
        this.router.navigate(['/register']); // Redirect to the login page
      }
    });
    
  }

  onKeyUpCheckPasswords(event: any){
    const typed = event.target.value;
    if(typed == this.password){
      this.passwordsMatch = true;
    } else {
      this.passwordsMatch = false;
    }
  }

}



